<template>
  <div class="titulo_pagina">
    <h1 class="titulo">Notícias</h1>
    <section>
      <transition mode="out-in">
        <div v-if="noticias && noticias.length" key="noticias">
          <div
            v-for="noticia in noticias"
            :key="noticia.Titulo"
            class="noticia_lista"
          >
            <img class="foto_noticia" src="../img/noticia1.jpg" />
            <div class="noticia_lista_div">
              <span class="categoria">{{ noticia.Categoria }}</span>
              <span class="data">{{ noticia.DataHoraCadastro }}</span>
              <img class="icone" src="../assets/compartilhar_rosa.svg" />
              <router-link
                class="noticia-lista_link"
                :to="{ name: 'noticia', params: { id: noticia.Titulo } }"
                >{{ noticia.Titulo }}</router-link
              >
              <span class="descricao">{{ noticia.Conteudo }}</span>
            </div>
          </div>
        </div>
        <PaginaCarregando key="carregando" v-else />
      </transition>
    </section>
  </div>
</template>

<script>
import { db } from '../firebase.js'

export default {
  name: 'noticias',
  data () {
    return {
      noticias: {}
    }
  },
  firestore: {
    noticias: db.collection('Noticias').orderBy('DataHoraCadastro', 'desc')
  },
  methods: {
    getNoticia (titulo) {
      db.collection('Noticias')
        .doc(titulo)
        .get()
    }
  }
}
</script>

<style scoped>
.noticia_lista {
  margin: 40px 69px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 40px;
}

.foto_noticia {
  margin: 0 40px 0 0;
}

.noticia_lista_div {
  grid: 2;
  justify-content: start;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: space-between;
  align-content: start;
  align-items: center;
}

.noticia_lista_div a {
  grid-column: span 3;
  display: block;
  margin-top: 10px;
  text-align: left;
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 2.25em;
  font-weight: 600;
  line-height: 1.1;
}

.noticia_lista_div a:hover {
  color: #000;
}

.descricao {
  flex: 1;
  grid-column: span 3;
  text-align: left;
  line-height: 1.5em;
  margin: 20px 0;
}

.categoria {
  justify-self: start;
}

.data {
  justify-self: center;
}

.icone {
  justify-self: end;
}

@media (max-width: 760px) {
  .noticia_lista {
    margin: 40px 16px 0 16px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 16px;
  }

  .foto_noticia {
    width: 100%;
    flex: 1;
    max-width: initial;
  }

  .noticia_lista_div span,
  .noticia_lista_div a {
    display: block;
    text-align: center;
  }
}
</style>



