<template>
  <transition mode="out-in">
    <section class="box_4grid">
      <div class="card_4grid">
        <img src="../img/contato1.jpg" />
        <div class="card_4grid_div">
          <h2 class="card_4grid_link">História da Arquidiocese</h2>
          <span class="telefone">27 3223-6711</span>
          <span class="email">mitra@aves.org.br</span>
        </div>
      </div>

      <div class="card_4grid">
        <img src="../img/contato2.jpg" />
        <div class="card_4grid_div">
          <h2 class="card_4grid_link">Cúria metropolitana</h2>
          <span class="telefone">27 3223-6711</span>
          <span class="email">mitra@aves.org.br</span>
        </div>
      </div>

      <div class="card_4grid">
        <img src="../img/contato3.jpg" />
        <div class="card_4grid_div">
          <h2 class="card_4grid_link">Vigários Gerais</h2>
          <span class="telefone">27 3223-6711</span>
          <span class="email">mitra@aves.org.br</span>
        </div>
      </div>

      <div class="card_4grid">
        <img src="../img/contato4.jpg" />
        <div class="card_4grid_div">
          <h2 class="card_4grid_link">Departamento Pastoral</h2>
          <span class="telefone">27 3223-6711</span>
          <span class="email">mitra@aves.org.br</span>
        </div>
      </div>

    </section>
  </transition>
</template>

<script>
export default {
  name: 'EstruturaGrid3'
}
</script>

<style scoped>
/* CARD COM 3 FOTOS NA LINHA */

.box_4grid {
  grid-area: box_4grid;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-items: auto;
  justify-content: space-between;
  gap: 40px;
  margin: 40px 0;
}

.card_4grid {
  flex: 1;
}

.card_4grid_div {
  display: grid;
  align-content: start;
}

.card_4grid_link {
  display: block;
  margin-top: 10px;
  text-align: center;
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.5em;
  font-weight: 600;
  line-height: 1.1;
}

.telefone, .email {
  margin: 10px 0 0 0;
}

.telefone {
  font-size: 1.3em;
  font-weight: 600;
}

@media (max-width: 960px) {
  .box_4grid {
    grid-template-columns: 1fr 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
  .card_4grid img {
    margin: 0 auto;
  }
  .card_4grid_div_link {
    margin-top: 2px;
  }
}

@media (max-width: 760px) {
  .box_4grid {
    grid-template-columns: 1fr;
    justify-items: auto;
    justify-content: space-between;
  }
}
/*****************************/

.v-enter,
.v-leave-to {
  opacity: 0;
}

.v-enter {
  transform: translate3d(-20px, 0, 0);
}

.v-leave-to {
  transform: translate3d(20px, 0, 0);
}

.v-enter-active {
  /* .v-leave-active {*/
  transition: all 0.5s;
}
</style>



