<template>
  <section class="box_6grid">
    <a class="link" href="#">
    <div class="card_6grid">
        <img src="../img/area1.jpg" />
      <h2 class="card_6grid_titulo">Benevente</h2>
      <p>11 paróquias</p>
    </div>
    </a>

  <a class="link" href="#">
    <div class="card_6grid">
      <a href="#">
        <img src="../img/area2.jpg" />
      </a>
      <h2 class="card_6grid_titulo">Cariacica - Viana</h2>
      <p>18 paróquias</p>
    </div>
  </a>

<a class="link" href="#">
    <div class="card_6grid">
      <a href="#">
        <img src="../img/area3.jpg" />
      </a>
      <h2 class="card_6grid_titulo">Serra - Fundão</h2>
      <p>19 paróquias</p>
    </div>
</a>

<a class="link" href="#">
    <div class="card_6grid">
      <a href="#">
        <img src="../img/area4.jpg" />
      </a>
      <h2 class="card_6grid_titulo">Serrana</h2>
      <p>09 paróquias</p>
    </div>
</a>

<a class="link" href="#">
    <div class="card_6grid">
      <a href="#">
        <img src="../img/area5.jpg" />
      </a>
      <h2 class="card_6grid_titulo">Vila Velha</h2>
      <p>20 paróquias</p>
    </div>
</a>

<a class="link" href="#">
    <div class="card_6grid">
      <a href="#">
        <img src="../img/area6.jpg" />
      </a>
      <h2 class="card_6grid_titulo">Vitória</h2>
      <p>14 paróquias</p>
    </div>
</a>
  </section>
</template>

<script>
export default {
  name: 'HomeAreasPastorais'
}
</script>

<style scoped>
/* CARD COM 6 FOTOS NA LINHA */

.box_titulo {
  grid-area: box_titulo;
  text-align: center;
  margin-bottom: 20px;
  position: relative;
}

.box_titulo span {
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 2.2em;
  border-bottom: 1px solid #eab883;
  font-weight: 700;
}

.box_6grid {
  grid-area: box_6grid;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  justify-items: auto;
  justify-content: space-between;
  grid-column-gap: 40px;
  margin: 0 0 40px 0;
}

.card_6grid {
  flex: 1;
  text-align: center;
  min-width: 150px;
}

.card_6grid_titulo {
  font-family: "Cormorant Garamond", serif;
  color: #4a5a69;
  font-size: 1.4em;
  font-weight: 700;
  padding: 10px 0 5px 0;
}

.card_6grid p {
  color: #b4ae8a;
  font-size: 0.875em;
  font-weight: 600;
}

@keyframes seta {
  from {
    right: 30px;
  }
  to {
    right: 20px;
  }
}

@media (max-width: 960px) {
  .box_6grid {
    position: relative;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    grid-column-gap: 25px;
    margin: 0 0 20px 0;
  }
  .card_6grid {
    margin-bottom: 16px;
  }

  .card_6grid img {
    min-width: 98px;
  }
  .card_6grid_titulo {
    font-size: 1.2em;
    font-weight: 700;
  }
  .card_6grid p {
    font-size: 0.75em;
  }
  .box_titulo::after {
    content: "→";
    position: absolute;
    top: 5px;
    right: 30px;
    color: rgba(0, 0, 0, 0.3);
    animation: seta 0.5s ease-in 0s infinite alternate;
  }
}
/*****************************/
</style>



